import React from 'react';

class TabComp extends React.PureComponent {
  handleChange = (index) => {
    this.setState({
        objSelected: index
    });
  }

  render() {
    const { selected, children } = this.props;
    const { objSelected } = this.state;
    return (
      <div>
        <ul className="inline">
          {children.map((elem, index) => {
            const style = index === selected ? 'selected' : '';
            return (
              <li className={style} key={elem.props.uniqueKey} onClick={this.handleChange(this, index)}> 
                {elem.props.title}
              </li>
            );
          })}
        </ul>
        TabComp 
        {selected}
        {objSelected 
        ? objSelected
        : 'not working'}
        {/* {children.map((elem) => (
          console.log(elem.props.title)
            ))} */}
      </div>
    );
  }
}

export default TabComp;
